.header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 10px;
}

.main-menu {
  width: 1200px;
  height: 80px;
  padding: 0;
  margin: 0 auto;

  display: grid;
  grid-template-areas:
    "left right";
  justify-content: space-between;
  align-items: center;
}

.left-pannel {
  grid-area: left;
  display: grid;
  grid-template-areas:
    "logo location deliver";
  align-items: center;
}

.right-pannel {
  grid-area: right;
  display: grid;
  grid-template-areas:
    "search login";
  align-items: center;
}

.header_logo {
  grid-area: logo;
}

.header_location {
  grid-area: location;

  margin-left: 70px;
}

.header_location-input, .header_search-input {
  box-sizing: border-box;
  width: 100px;

  background-size: 20px;
  background-repeat: no-repeat;
  background-position: 15px 10px;

  padding-left: 45px;
  background-color: #fff;
  color: #000;
  border: none;
  border-bottom: 2px solid #fff;


  font-size: 16px;
  line-height: 40px;
  outline: none;

  transition: width 500ms ease-in-out;
}

.header_location-input {
  background-image: url('../pic/Location.svg');
}

.header_location-input:focus, .header_search-input:focus {
  background-color: #f5f5f5;
  border-bottom: 2px solid #000;
  width: 450px;
}

.header_deliver {
  grid-area: deliver;
  margin-left: 60px;
}

.header_deliver-select {
  appearance: none;
  background-color: #fff;
  color: #000;
  border: none;
  border-bottom: 2px solid #fff;
  width: 120px;
  height: 40px;


  font-size: 16px;
  line-height: 40px;
  text-decoration: none;
  outline: none;
}

.header_deliver-select:focus {
  background-color: #f5f5f5;
  border-bottom: 2px solid #000;
}

.header_search {
  grid-area: search;
}

.header_search-input {
  background-image: url('../pic/search.svg');
}

.header_login {
  grid-area: login;
  margin-left: 20px;
}

.header_login-ref {
  text-decoration: none;
  font-size: 16px;
  color: #000;
}

