@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic);
.header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 10px;
}

.main-menu {
  width: 1200px;
  height: 80px;
  padding: 0;
  margin: 0 auto;

  display: grid;
  grid-template-areas:
    "left right";
  justify-content: space-between;
  align-items: center;
}

.left-pannel {
  grid-area: left;
  display: grid;
  grid-template-areas:
    "logo location deliver";
  align-items: center;
}

.right-pannel {
  grid-area: right;
  display: grid;
  grid-template-areas:
    "search login";
  align-items: center;
}

.header_logo {
  grid-area: logo;
}

.header_location {
  grid-area: location;

  margin-left: 70px;
}

.header_location-input, .header_search-input {
  box-sizing: border-box;
  width: 100px;

  background-size: 20px;
  background-repeat: no-repeat;
  background-position: 15px 10px;

  padding-left: 45px;
  background-color: #fff;
  color: #000;
  border: none;
  border-bottom: 2px solid #fff;


  font-size: 16px;
  line-height: 40px;
  outline: none;

  -webkit-transition: width 500ms ease-in-out;

  transition: width 500ms ease-in-out;
}

.header_location-input {
  background-image: url(/react_uber-eats/static/media/Location.6bbd6042.svg);
}

.header_location-input:focus, .header_search-input:focus {
  background-color: #f5f5f5;
  border-bottom: 2px solid #000;
  width: 450px;
}

.header_deliver {
  grid-area: deliver;
  margin-left: 60px;
}

.header_deliver-select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: #fff;
  color: #000;
  border: none;
  border-bottom: 2px solid #fff;
  width: 120px;
  height: 40px;


  font-size: 16px;
  line-height: 40px;
  text-decoration: none;
  outline: none;
}

.header_deliver-select:focus {
  background-color: #f5f5f5;
  border-bottom: 2px solid #000;
}

.header_search {
  grid-area: search;
}

.header_search-input {
  background-image: url(/react_uber-eats/static/media/search.d3cc30e9.svg);
}

.header_login {
  grid-area: login;
  margin-left: 20px;
}

.header_login-ref {
  text-decoration: none;
  font-size: 16px;
  color: #000;
}


.card {
}

.rest-pic {
  height: 240px;
  overflow: hidden;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.rest-pic > img {
  max-width:100%;
}

.rest-name {
  margin-top: 12px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #1F1F1F;
  line-height: 24px;
  font-size: 16px;
  display: block;
}

.rest-category {
  max-width: 330px;
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: rgb(117, 117, 117);
  line-height: 20px;
  font-size: 14px;
}

.rest-details {
  margin-top: 5px;
  display: flex;
  flex-direction: row;
}

.rest-delivery, .rest-rating {
  height: 30px;
  background-color: #f5f5f5;
  font-size: 14px;
  line-height: 30px;
  margin-right: 10px;
}

.star {
  color: #ffd900;
  font-size: 22px;
}

.rest-endorcement {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #757575;
}
.rest-endorcement > img {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

.rest-list {
  width: 1200px;
  padding: 0;
  margin: 110px auto;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px 25px;
}



.mainPage {
  width: 100%;
}


.footer {
  width: 100%;
  background-color: #1f1f1f;
  color: #fff;
}

.footer_container {
  width: 1200px;
  padding: 60px 0;
  margin: 0 auto;

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    "footer_logo footer_menu"
    "footer_mobile footer_menu"
    "footer_devider footer_devider"
    "footer_copyrights footer_terms"
    "footer_copyrights footer_socials";
  justify-content: start;
  align-items: start;
}

.footer_logo {
  grid-area: footer_logo;
}

.footer_logo > SVG > .logo-part-uber {
  fill: #fff;
}

.footer_mobile {
  grid-area: footer_mobile;
  align-self: end;
}

.footer_mobile > a {
  margin-right: 15px;
}

.footer_devider {
  grid-area: footer_devider;
  width: 100%;
  height: 1px;
  margin: 50px 0;
  background-color: #fff;
}

.footer_menu {
  grid-area: footer_menu;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    "footer_menu1 footer_menu2";
  justify-items: start;
  align-items: start;
}

.footer_menu1 {
  grid-area: footer_menu1;
}

.footer_menu2 {
  grid-area: footer_menu2;
}

.footer_menu-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.footer_menu-item {
  margin-top: 25px;
}

.footer_menu-item:first-child {
  margin-top: 0;
}

.footer_menu_link {
  text-decoration: none;
  color: #fff;
  font-size: 16px;
}

.footer_menu_link:hover {
  text-decoration: underline;
}

.footer_copyrights {
  grid-area: footer_copyrights;
  font-size: 14px;
}

.footer_terms {
  grid-area: footer_terms;
}

.hor_li {
  display: inline;
  margin-right: 50px;
}

.footer_socials {
  grid-area: footer_socials;
  margin-top: 60px;
}

body {
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  font-size: 11px;
  line-height: 13px;
}



