.card {
}

.rest-pic {
  height: 240px;
  overflow: hidden;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.rest-pic > img {
  max-width:100%;
}

.rest-name {
  margin-top: 12px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #1F1F1F;
  line-height: 24px;
  font-size: 16px;
  display: block;
}

.rest-category {
  max-width: 330px;
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: rgb(117, 117, 117);
  line-height: 20px;
  font-size: 14px;
}

.rest-details {
  margin-top: 5px;
  display: flex;
  flex-direction: row;
}

.rest-delivery, .rest-rating {
  height: 30px;
  background-color: #f5f5f5;
  font-size: 14px;
  line-height: 30px;
  margin-right: 10px;
}

.star {
  color: #ffd900;
  font-size: 22px;
}

.rest-endorcement {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #757575;
}
.rest-endorcement > img {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}
