.footer {
  width: 100%;
  background-color: #1f1f1f;
  color: #fff;
}

.footer_container {
  width: 1200px;
  padding: 60px 0;
  margin: 0 auto;

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    "footer_logo footer_menu"
    "footer_mobile footer_menu"
    "footer_devider footer_devider"
    "footer_copyrights footer_terms"
    "footer_copyrights footer_socials";
  justify-content: start;
  align-items: start;
}

.footer_logo {
  grid-area: footer_logo;
}

.footer_logo > SVG > .logo-part-uber {
  fill: #fff;
}

.footer_mobile {
  grid-area: footer_mobile;
  align-self: end;
}

.footer_mobile > a {
  margin-right: 15px;
}

.footer_devider {
  grid-area: footer_devider;
  width: 100%;
  height: 1px;
  margin: 50px 0;
  background-color: #fff;
}

.footer_menu {
  grid-area: footer_menu;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    "footer_menu1 footer_menu2";
  justify-items: start;
  align-items: start;
}

.footer_menu1 {
  grid-area: footer_menu1;
}

.footer_menu2 {
  grid-area: footer_menu2;
}

.footer_menu-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.footer_menu-item {
  margin-top: 25px;
}

.footer_menu-item:first-child {
  margin-top: 0;
}

.footer_menu_link {
  text-decoration: none;
  color: #fff;
  font-size: 16px;
}

.footer_menu_link:hover {
  text-decoration: underline;
}

.footer_copyrights {
  grid-area: footer_copyrights;
  font-size: 14px;
}

.footer_terms {
  grid-area: footer_terms;
}

.hor_li {
  display: inline;
  margin-right: 50px;
}

.footer_socials {
  grid-area: footer_socials;
  margin-top: 60px;
}
